.clients {
  position: relative;
  padding: 20px 0;
  border-radius: 16px;
  background-color: $gray-200;

  @include screen(tablet) {
    border-radius: 19px;
    padding: 24px 0;
  }

  @include screen(tablet-2x) {
    border-radius: $border-radius;
    padding: 45px 0;
  }

  &__swiper-wrapper {
    transition-timing-function: linear;
    align-items: center;
    .swiper-slide {
      transition: filter $transition-default;
      filter: blur(4px);
      
      &-active {
        filter: blur(0);
        & + .swiper-slide,
        & + .swiper-slide + .swiper-slide {
          filter: blur(0);
        }
        @include screen(tablet-2x) {
          & + .swiper-slide,
          & + .swiper-slide + .swiper-slide,
          & + .swiper-slide + .swiper-slide + .swiper-slide {
            filter: blur(0);
          }
        }
      }
    }
  }

  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__img {
    padding: 10px 0;
    max-height: 38px;
    width: auto;
    @include screen(tablet) {
      max-height: 45px;
    }

    @include screen(tablet-2x) {
      max-height: 75px;
    }
  }
}

