.home {
  margin-bottom: 16px;
  @include screen(tablet) {
    margin-bottom: 18px;
  }
  @include screen(tablet-2x) {
    margin-bottom: 32px;
  }
  & .card__title {
    font-weight: 800;
    font-size: 16px;
    line-height: 100%;
    font-family: 'RFDEWI';
    margin-bottom: 0;
    color: $gray-200;
    max-width: 200px;

    @include screen(tablet) {
      max-width: none;
      font-size: 21px;
    }

    @include screen(tablet-2x) {
      max-width: none;
      font-size: 41px;
    }
  }

  & .card__icon {
    width: 6px;
    height: 6px;
    right: 10px;
    top: 10px;

    @include screen(tablet-2x) {
      width: 10px;
      height: 10px;
      top: 20px;
      right: 20px;
    }
  }

  & .card__image {
    width: 100%;
    height: 100%;

    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }

  & .top-card .card__content {
    max-width: 273px;

    @include screen(tablet) {
      max-width: 315px;
    }

    @include screen(tablet-2x) {
      max-width: 588px;
    }

  }

  & .top-card .card__title {
    margin-bottom: 12px;
    font-weight: 900;
    font-size: 24px;
    color: $black-bg;

    @include screen(tablet) {
      font-size: 30px;
    }

    @include screen(tablet-2x) {
      font-size: 58px;
    }
  }

  & .top-card .card__description {
    @include screen(tablet) {
      margin-bottom: 21px;
      font-size: 14px;
    }

    @include screen(tablet-2x) {
      margin-bottom: 40px;
      font-size: 27px;
    }
  }

  &-logo {
    grid-row-start: 4;
    @include screen(tablet) {
      grid-row-start: 2;
    }
  }
}

.video {
  padding: 0;
  &__video {
    width: 100%;
    height: 100%;
    object-fit: cover;

  }
}

