.home-slider {
  padding: 0;
  display: block;

  &__box {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__swiper {
    width: 100%;
    height: 100%;
  }

  &__slide {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__block {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 126px;
    padding: 16px 12px;
    display: flex;
    align-items: flex-end;
    background-image: linear-gradient(transparent, $black-bg);
    @include screen(tablet) {
      height: 141px;
      padding: 24px 14px;
    }
    @include screen(tablet-2x) {
      display: block;
      height: 253px;
      padding: 40px 24px;
      background-image: none;
      background-color: $blue-bg;
    }
  }

  &__title {
    font-family: "RFDEWI";
    font-size: 15px;
    font-weight: 900;
    line-height: 82%;
    @include screen(tablet-2x) {
      margin-bottom: 32px;
      font-size: 28px;
    }
  }

  &__descr {
    display: none;
    font-size: 16px;
    line-height: 100%;
    @include screen(tablet-2x) {
      display: block;
    }
  }

  &__btn-box {
    display: none;
    position: absolute;
    width: 100%;
    z-index: 10;
    justify-content: center;
    gap: 105px;
    @include screen(tablet-2x) {
      display: flex;
      bottom: 33px;
    }
    @include screen(laptop) {
      bottom: 43px;
    }
  }

  &__btn {
    & svg {
      transition: opacity $transition-default;
    }
    &:hover svg {
      opacity: 0.7;
    }
    &:active svg {
      opacity: 0.7;
    }
  }


}