.header {
  padding-top: 24px;
  position: relative;
  z-index: 100;

  &__logo {
    display: inline-block;
  }

  &__box {
    max-height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include screen(tablet) {
      padding: 5px 25px;
      padding-right: 6px;
      border-radius: 19px;
      background-color: $black-bg;
      box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.05), 5px 5px 7px 0 rgba(0, 0, 0, 0.04), 11px 11px 10px 0 rgba(0, 0, 0, 0.03), 20px 20px 11px 0 rgba(0, 0, 0, 0.01), 31px 31px 12px 0 rgba(0, 0, 0, 0);
    }

    @include screen(tablet-2x) {
      padding: 10px 47px;
      padding-right: 11px;
      border-radius: $border-radius;
    }
  }

  &__img {
    @include screen(tablet) {
      width: 140px;
      height: 15px;
    }

    @include screen(tablet-2x) {
      width: 261px;
      height: 29px;
    }
  }

  &__nav {
    display: none;
    position: relative;
    @include screen(tablet) {
      display: block;
    }
  }

  &__list {
    display: none;
    // position: absolute;
    // top: 0;
    // right: 0;
    // padding: 10px;
    border-radius: $border-radius;
    background-color: $black-bg;
    // z-index: -1;

    @include screen(tablet) {
      position: static;
      display: flex;
      gap: 8px;
      padding: 0;
    }
  }

  &__link {
    position: relative;
    display: block;
    z-index: 20;

    & > a {
      border-radius: $border-radius;
      display: inline-block;
      z-index: 20;
      padding: 21px 35px;
      font-family: 'Roboto Mono';
      font-size: 24px;
      @include screen(tablet) {
        padding: 7px 18px;
        font-size: 12px;
      }

      @include screen(tablet-2x) {
        padding: 9px 35px;
        font-size: 18px;
      }

      @include screen(laptop) {
        font-size: 24px;
      }
    }
    @include screen(tablet) {
      &:hover {
        & > a {
          background-color: $green-bg;
          color: $black-bg;
        }
        .header__block {
          opacity: 1;
          visibility: visible;
          z-index: 10;
        }
      }
    }

    &-tel {
      display: inline-block;
      font-weight: 700;
      font-size: 14px;
      @include screen(tablet-2x) {
        font-size: 16px;
      }
      @include screen(laptop) {
        transition: opacity $transition-default;
        &:hover {
          opacity: 0.5;
        }
      }
    }

    &-active {
      background-color: $green-bg;
      color: $black-bg;
    }

    &-contacts,
    &-projects {
      margin-bottom: -10px;
      & > a {
        padding-bottom: 19px;
        @include screen(tablet) {
          border-radius: 15px 15px 0 0;
        }
        @include screen(tablet-2x) {
          border-radius: 25px 25px 0 0;
        }
        @include screen(laptop) {
          padding-bottom: 17px;
        }
      }
    }
    &-projects {
      &:before,
      &:after {
        opacity: 0;
        content: '';
        background-color: $black-bg;
        position: absolute;
        z-index: 10;
        bottom: 5px;
        width: 15px;
        height: 15px;
        @include screen(tablet-2x) {
          width: 25px;
          height: 25px;
        }
        @include screen(laptop) {
          width: 35px;
          height: 35px;
        }
      }
      &:before {
        box-shadow: 7px 7px 0 6px $green-bg;
        left: -15px;
        border-radius: 0 0 15px 0;
        @include screen(tablet-2x) {
          border-radius: 0 0 25px 0;
          left: -25px;
          bottom: 0;
        }
        @include screen(laptop) {
          border-radius: 0 0 35px 0;
          left: -35px;
        }
      }
      &:after {
        box-shadow: 7px 7px 0 6px $green-bg;
        transform: scale(-1, 1);
        border-radius: 0 0 15px 0;
        right: -15px;
        @include screen(tablet-2x) {
          border-radius: 0 0 25px 0;
          right: -25px;
          bottom: 0;
        }
        @include screen(laptop) {
          right: -35px;
          border-radius: 0 0 35px 0;
        }
      }
      @include screen(tablet) {
        &:hover {
          &:before,
          &:after {
            opacity: 1;
          }
        }
      }
    }
    &-contacts {
      &:before {
        opacity: 0;
        content: '';
        background-color: $black-bg;
        position: absolute;
        z-index: 10;
        bottom: 5px;
        box-shadow: 7px 7px 0 6px $green-bg;
        left: -15px;
        border-radius: 0 0 15px 0;
        width: 15px;
        height: 15px;
        @include screen(tablet-2x) {
          width: 25px;
          height: 25px;
          border-radius: 0 0 25px 0;
          left: -25px;
          bottom: 0;
        }
        @include screen(laptop) {
          width: 35px;
          height: 35px;
          border-radius: 0 0 35px 0;
          left: -35px;

        }
      }
      @include screen(tablet) {
        &:hover {
          &:before {
            opacity: 1;
          }
        }
      }
    }
  }

  &__block {
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    &-projects {
      position: absolute;
      top: 68px;
      width: 100%;
      padding: 18px;
      border-radius: $border-radius;
      background-color: $green-bg;
      color: $black-bg;
      z-index: 10;
      @include screen(tablet) {
        left: -110px;
      }
      @include screen(tablet-2x) {
        padding: 32px;
        left: -176px;
      }
    }
    &-contacts {
      position: absolute;
      right: 0;
      top: 68px;
      width: 100%;
      padding: 18px 24px;
      border-radius: $border-radius;
      border-top-right-radius: 0px;
      background-color: $green-bg;
      color: $black-bg;
    }
    @include screen(tablet) {
      top: 39px;
      width: 300px;
    }
    @include screen(tablet-2x) {
      padding: 24px;
      top: 55px;
      width: 512px;
    }
    @include screen(laptop) {
      top: 62px;
    }
  }

  &__contacts {
    display: grid;
    gap: 8px;
    @include screen(tablet-2x) {
      grid-template-columns: 193px 1fr;
      gap: 20px 10px;
    }
    @include screen(laptop) {
      grid-template-columns: 193px 1fr;
      gap: 25px 10px;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-family: 'Roboto Mono';
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;
    @include screen(tablet-2x) {
      font-size: 30px;
    }
    @include screen(laptop) {
      font-size: 40px;
    }

    &-small {
      font-weight: 700;
      font-size: 16px;
      @include screen(tablet-2x) {
        font-size: 24px;
      }
      @include screen(laptop) {
        font-size: 30px;
      }
    }
  }

  &__address {
    font-family: 'Roboto Mono';
    font-style: normal;
    text-transform: uppercase;
    display: inline-flex;
    align-items: flex-start;
    flex-direction: column;
  }

  &__span {
    display: inline-block;
    font-size: 14px;
    @include screen(tablet-2x) {
      font-size: 16px;
    }
    @include screen(laptop) {
      transition: opacity $transition-default;
      &:hover {
        opacity: 0.7;
      }
    }
  }

  &__projects {
    display: grid;
    gap: 8px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, 1fr);
      gap: 32px;
    }
    &__item {
      display: flex;
      justify-content: center;
      position: relative;
      align-items: center;
      line-height: 100%;
      padding: 12px;
      border-radius: 18px;
      text-align: center;
      font-family: 'Roboto Mono';
      font-size: 14px;
      background-color: $gray-200;
      @include screen(laptop) {
        font-size: 18px;
        padding: 16px;
        transition: background-color $transition-default;
        &:hover {
          background-color: $gray-300;
        }
      }
    }
  }
}

.social {
  align-self: end;
  justify-self: center;
  display: flex;
  gap: 24px;
  align-items: center;

  &__img {
    transition: opacity $transition-default;
  }

  &__link:hover .social__img {
    opacity: 0.7;
  }
}

.mobile {
  &__nav {
    @include screen(tablet) {
      display: none;
    }
  }

  &__btn {
    position: relative;
    width: 38px;
    height: 38px;
    border-radius: 8px;
    background-color: $green-bg;
    z-index: 10;

    @include screen(tablet) {
      display: none;
    }

    & span {
      display: block;
      position: absolute;
      height: 2px;
      width: 20px;
      background-color: $black;
      opacity: 1;
      left: 9px;
      transform: rotate(0deg);
      transition: $transition-default;

      &:nth-child(1) {
        top: 12px;
      }

      &:nth-child(2),
      &:nth-child(3) {
        top: 18px;
      }

      &:nth-child(4) {
        top: 24px;
      }
    }

    &-open span:nth-child(1) {
      top: 18px;
      width: 0%;
      left: 50%;
    }

    &-open span:nth-child(2) {
      transform: rotate(45deg);
    }

    &-open span:nth-child(3) {
      transform: rotate(-45deg);
    }

    &-open span:nth-child(4) {
      top: 18px;
      width: 0%;
      left: 50%;
    }
  }
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  font-family: "Roboto Mono";
  font-size: 16px;
  background-color: $black-bg;
  opacity: 0;
  visibility: hidden;
  z-index: -100;
  transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;

  &-open {
    opacity: 1;
    visibility: visible;
    z-index: 0;
  }

  &__list {
    margin: 0 auto;
    padding: 90px 20px;
    max-width: 375px;
  }

  &__link {
    border-radius: $border-radius;
    padding: 5px 20px;
    transition: color $transition-default, background-color $transition-default;

    &:active {
      background-color: $green-bg;
      color: $black-bg;
    }

    &-active {
      background-color: $green-bg;
      color: $black-bg;
    }

    &-active .menu__arrow {
      &::before {
        background-color: $black-bg;
        transform: translateY(-2px) rotate(-45deg);
      }

      &::after {
        background-color: $black-bg;
        transform: translateY(-2px) rotate(45deg);
      }
    }
  }

  &__arrow {
    position: relative;
    padding-left: 15px;
    &::before {
      content: '';
      position: relative;
      top: -3px;
      display: inline-block;
      width: 7px;
      height: 2px;
      transform: rotate(45deg);
      background-color: $white;
      transition: transform $transition-default, background-color $transition-default;
    }
    &::after {
      content: '';
      position: relative;
      top: -3px;
      left: -3px;
      display: inline-block;
      width: 7px;
      height: 2px;
      transform: rotate(-45deg);
      background-color: $white;
      transition: transform $transition-default, background-color $transition-default;
    }
  }

  &__projects {
    padding: 10px 0;

    &__item {
      display: block;

      &:not(:last-child) {
        margin-bottom: 5px;
      }

      & span {
        border-bottom: 1px solid transparent;
        transition: border-bottom $transition-default;
      }

      &:active span {
        border-bottom: 1px solid $black-bg;
      }
    }
  }

  &__block {
    visibility: hidden;
    opacity: 0;
    max-height: 0;
    height: 100%;
    transition: opacity $transition-default, visibility $transition-default, max-height $transition-default;

    &-open {
      max-height: 400px;
      visibility: visible;
      opacity: 1;
    }

    &-contacts {
      text-align: left;
    }
  }
  &__contacts {
    margin-bottom: 15px;
  }

  &__title {
    margin-bottom: 15px;
    padding-top: 10px;
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
  }

  &__title-small {
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
  }

  &__address {
    margin-bottom: 10px;
    font-style: normal;
  }

  &__span {
    display: block;
  }

  &__link-tel {
    font-weight: 700;
  }

  &__social {
    display: flex;
    gap: 20px;
    align-items: center;
  }
}

