.footer {
  margin-bottom: 20px;
  @include screen(laptop) {
    margin-bottom: 64px;
  }
  &__box {
    padding: 9px 11px;
    padding-left: 24px;
    padding-bottom: 24px;
    border-radius: $border-radius;
    background-image: url('/assets/images/footer-bg.png');
    background-position: -10px top;
    background-repeat: no-repeat;
    height: 58px;
    transition: height $transition-default;
    overflow: hidden;

    @include screen(tablet-2x) {
      height: 78px;
      padding-bottom: 44px;
      background-position: -4px top;
    }

    &-open {
      height: 380px;

      @include screen(tablet) {
        height: 360px;
      }

      @include screen(tablet-2x) {
        height: 424px;
      }
    }

    &-top {
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-bottom {
      padding-top: 40px;

      @include screen(tablet-2x) {
        display: flex;
        justify-content: space-between;
        padding-top: 80px;
      }
    }
  }

  &__logo {
    display: block;
    width: 170px;
    height: 11px;
    object-fit: cover;

    @include screen(tablet) {
        width: 191px;
        height: 13px;
    }

    @include screen(tablet-2x) {
      width: 318px;
      height: 21px;
    }
  }

  &__icon {
    width: 40px;
    height: 40px;
    background-image: url('/assets/images/footer-icon.svg');
    background-size: cover;

    @include screen(tablet-2x) {
        width: 60px;
        height: 60px;
    }
  }

  &__block-left {
    max-width: 409px;
    font-family: "Roboto Mono";
    font-weight: 500;
    font-size: 16px;

    @include screen(tablet-2x) {
      font-size: 20px;
    }
  }

  &__address {
    margin-bottom: 40px;
    display: inline-block;
    font-style: normal;

    @include screen(tablet-2x) {
      margin-bottom: 44px;
    }
    @include screen(laptop) {
      transition: color $transition-default;
      &:hover {
        color: $green-bg;
      }
    }
  }

  &__tel,
  &__mail {
    display: inline-block;
    font-family: "Roboto Mono";
    font-weight: 500;
    font-size: 16px;

    @include screen(tablet-2x) {
      font-size: 20px;
    }
    @include screen(laptop) {
      transition: color $transition-default;
      &:hover {
        color: $green-bg;
      }
    }
  }

  &__tel {
    margin-bottom: 40px;

    @include screen(tablet-2x) {
      margin-bottom: 70px;
    }
  }

  &__mail {
    margin-bottom: 40px;

    @include screen(tablet-2x) {
      margin-bottom: 0px;
    }
  }

  &__block-right {
    display: flex;
    align-items: end;
    gap: 26px;

    @include screen(tablet-2x) {
      padding: 0 28px;
    }
  }

  &__img-social {
    transition: opacity $transition-default;
  }

  &__link:hover &__img-social {
    opacity: 0.7;
  }
}