.tabs {
  margin-bottom: 32px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  @include screen(tablet-2x) {
    margin-bottom: 40px;
    column-gap: 20px;
    row-gap: 24px;
  }

  &__btn {
    padding: 10px 14px;
    border-radius: $border-radius-mob;
    border: 1px solid transparent;
    font-family: "Roboto Mono";
    font-size: 9px;
    color: $black-bg;
    box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.05), 7px 7px 9px 0 rgba(0, 0, 0, 0.04), 15px 15px 13px 0 rgba(0, 0, 0, 0.03), 27px 27px 15px 0 rgba(0, 0, 0, 0.01), 42px 42px 16px 0 rgba(0, 0, 0, 0);
    background-color: $gray-200;
    transition: border $transition-default, background-color $transition-default;

    @include screen(tablet) {
      font-size: 12px;
      border-radius: $border-radius-tab;
    }

    @include screen(tablet-2x) {
      padding: 20px 32px;
      font-size: 20px;
      border-radius: $border-radius;
    }

    &:hover {
      border: 1px solid $black-bg;
    }

    &-active {
      background-color: $green-bg;
    }
  }

  &-content span {
    color: $black-bg;
  }
}