.card {
  background-color: $gray-200;
  padding: 12px;
  border-radius: $border-radius-mob;
  min-height: 169px;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: flex-end;

  @include screen(tablet) {
    padding: 13px;
    min-height: 188px;
    border-radius: $border-radius-tab;
  }
  @include screen(tablet-2x) {
    padding: 24px;
    min-height: 352px;
    border-radius: $border-radius;
  }

  &.col-span-4 {
    grid-column: span 6 / span 6;
    @include screen(tablet) {
      grid-column: span 4 / span 4;
    }
  }
  &.col-span-6 {
    grid-column: span 12 / span 12;
    @include screen(tablet) {
      grid-column: span 6 / span 6;
    }
  }
  &.col-span-8 {
    grid-column: span 12 / span 12;
    @include screen(tablet) {
      grid-column: span 8 / span 8;
    }
  }
  &.col-span-12 {
    grid-column: span 12 / span 12;
  }
  &.row-span-2 {
    grid-row: span 2 / span 2;
  }
  &.small-row {
    padding: 12px;
    min-height: 72px;
    align-items: center;

    @include screen(tablet) {
      padding: 13px 19px;
      min-height: 83px;
    }

    @include screen(tablet-2x) {
      padding: 36px 24px;
      min-height: 159px;
    }
  }
  &.big-row {
    min-height: 198px;
    @include screen(tablet) {
      min-height: 259px;
    }

    @include screen(tablet-2x) {
      min-height: 485px;
    }
  }
  &.big-video-row {
    max-height: 353px;
    @include screen(tablet) {
      max-height: 336px;
    }

    @include screen(tablet-2x) {
      max-height: 630px;
    }
  }
  &__image {
    position: absolute;
    left: 0;
    top: 0;
  }
  &__content {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 3;
  }
  &__title {
    font-size: 14px;
    font-weight: 700;
    color: $text;
    line-height: 100%;
    margin-bottom: 16px;
    @include screen(tablet) {
      font-size: 20px;
      margin-bottom: 22px;
    }
    @include screen(tablet-2x) {
      font-size: 32px;
      margin-bottom: 24px;
    }
  }
  &__description {
    font-size: 8px;
    line-height: 110%;
    color: $text;
    @include roboto;
    @include screen(tablet-2x) {
      font-size: 16px;
    }
  }
  &__icon {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 3;
    @include screen(tablet) {
      right: 11px;
      top: 11px;
    }
    @include screen(tablet-2x) {
      right: 24px;
      top: 24px;
    }
  }
  &__logo {
    display: none;
    position: absolute;
    left: 24px;
    top: 30px;
    z-index: 3;
    @include screen(laptop) {
      display: block;
    }
  }
  &-with-fade {
    overflow: hidden;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($black, 0.5);
      z-index: 2;
    }
    .card__image {
      width: 100%;
      height: 100%;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
  }
  &-with-half-fade {
    overflow: hidden;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(transparent, $black 70%);
      opacity: 0.5;
      z-index: 2;
    }

    .card__image {
      width: 100%;
      height: 100%;

      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
  }
  &-title-top {
    .card__content {
      padding-top: 10px;
      height: 100%;
      justify-content: flex-start;
    }
  }
  &-case-block {
    overflow: hidden;
    position: relative;
    .card__image {
      width: 100%;
      height: 100%;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
  }
  &-white {
    .card__title {
      color: $white;
    }
    .card__description {
      color: $white;
    }
  }
  &__tags {
    position: absolute;
    top: 15px;
    right: 16px;
    display: flex;
    align-items: center;
    gap: 5px;
    z-index: 2;
    @include screen(tablet-2x) {
      top: 32px;
      right: 24px;
      gap: 10px;
    }
  }
  &__tag {
    background-color: $green-bg;
    border-radius: 15px;
    padding: 4px 8px;
    font-family: "Roboto Mono";
    font-size: 8px;
    color: $black-bg;
    @include screen(tablet-2x) {
      padding: 8px 16px;
      font-size: 12px;
    }
    @include screen(laptop) {
      border-radius: $border-radius;
    }
  }
  &__box {
    display: flex;
    align-items: center;
    gap: 10px;
    max-width: 100%;

    img {
      max-width: calc(100% / 3);
      @include screen(tablet-2x) {
        max-width: calc(100% / 2);
      }
    }
  }
  &-smaller-mob {
    min-height: 137px;
    @include screen(tablet) {
      min-height: 188px;
    }
    @include screen(tablet-2x) {
      min-height: 352px;
    }
  }

  &-directions {
    overflow: hidden;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($black, 0.5);
      z-index: 2;
    }
    .card__title {
      font-family: 'RFDEWI';
      font-weight: 800;
      font-size: 11px;
      margin-bottom: 3px;
      color: $white;
      @include screen(tablet) {
        margin-bottom: 5px;
        font-size: 16px;
      }
      @include screen(tablet-2x) {
        margin-bottom: 16px;
        font-size: 24px;
      }
    }
    .card__description {
      color: $white;
    }
    .card__image {
      width: 100%;
      height: 100%;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
  }
}