.about {
  &__card {
    align-items: flex-start;
    padding: 16px 12px;
    @include screen(tablet) {
      padding: 27px 14px;
    }
    @include screen(tablet-2x) {
      padding: 50px 24px;
    }

    & .card__content {
      height: 100%;
      justify-content: space-between;
    }

    & .card__description {
      max-width: 305px;
      font-size: 12px;
      color: $white;
      @include screen(tablet) {
        max-width: 523px;
        font-size: 16px;
      }

      @include screen(tablet-2x) {
        max-width: 975px;
        font-size: 30px;
      }
    }
    &-about {
      & .card__title {
        font-family: "Roboto";
        font-weight: 700;
      }
    }

    &-year {
      & .card__content {
        padding-top: 60px;
        justify-content: flex-start;

        @include screen(tablet-2x) {
          padding-top: 100px;
        }
      }

      & .card__title {
        margin-bottom: 10px;
        font-family: 'RFDEWI';
        font-weight: 900;
        font-size: 24px;
        line-height: 100%;
        color: $blue-bg;
        @include screen(tablet) {
          margin-bottom: 11px;
          font-size: 30px;
        }
        @include screen(tablet-2x) {
          margin-bottom: 20px;
          font-size: 58px;
        }
      }

      & .card__description {
        color: $black-bg;
        font-size: 16px;
        @include screen(tablet) {
          font-size: 14px;
        }

        @include screen(tablet-2x) {
          font-size: 28px;
        }
      }
    }

    &-geography {
      @include screen(tablet) {
        background-color: $black-bg;
      }
      & .card__content {
        width: 100%;
      }

      & .card__image {
        @include screen(tablet) {
          display: none;
        }
      }

      & .card__title {
        font-family: "Roboto";
        font-weight: 700;
        font-size: 16px;
        @include screen(tablet) {
          font-size: 20px;
        }
        @include screen(tablet-2x) {
          font-size: 38px;
        }

        &-tab {
          display: none;
          @include screen(tablet) {
            display: block;
          }
        }
        &-mob {
          @include screen(tablet) {
            display: none;
          }
        }
      }
      & .card__list {
        display: flex;
        gap: 10px;
        justify-content: space-between;
        @include screen(tablet) {
          padding: 0 19px;
        }
        @include screen(tablet-2x) {
          padding: 0 37px;
        }
      }
      & .card__item:not(:first-child) {
        display: none;
        @include screen(tablet) {
          display: block;
        }
      }
      & .card__country {
        display: none;
        @include screen(tablet) {
          display: block;
          margin-bottom: 5px;
          font-family: "Roboto Mono";
          font-weight: 700;
          font-size: 26px;
          color: $green-bg;
        }
        @include screen(tablet-2x) {
          font-size: 50px;
        }
      }
      & .card__city {
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 30px;
        color: $green-bg;
        @include screen(tablet) {
          margin-bottom: 0px;
          font-weight: 700;
          font-size: 17px;
          color: $white;
        }
        @include screen(tablet-2x) {
          font-size: 32px;
        }
      }
      & .card__link {
        display: block;
        font-family: "Roboto Mono";
        line-height: 120%;
        &-address {
          margin-bottom: 18px;
          font-weight: 400;
          font-size: 16px;
          @include screen(tablet) {
            margin-bottom: 12px;
            font-size: 7px;
          }
          @include screen(tablet-2x) {
            font-size: 14px;
          }
        }
        &-tel {
          font-weight: 500;
          font-size: 16px;
          @include screen(tablet) {
            font-size: 13px;
          }
          @include screen(tablet-2x) {
            font-size: 25px;
          }
        }
      }
    }
    &-success {
      min-height: 72px;
      grid-column: span 12 / span 12;
      @include screen(tablet) {
        min-height: 188px;
        grid-column: span 4 / span 4;
      }
      @include screen(tablet-2x) {
        min-height: 352px;
        grid-column: span 8 / span 8;
      }
      & .card__content {
        justify-content: center;
        @include screen(tablet) {
          justify-content: flex-end;
        }
        @include screen(tablet-2x) {
          justify-content: space-between;
        }
      }
      & .card__title {
        display: none;
        @include screen(tablet-2x) {
          display: block;
          max-width: 388px;
          margin-bottom: 10px;
          font-family: "Roboto";
          font-weight: 700;
          font-size: 32px;
          line-height: 85%;
        }
      }
      & .card__descr-none {
        display: none;
        @include screen(tablet-2x) {
          display: block;
        }
      }
      & .card__description {
        font-size: 15px;
        @include screen(tablet) {
          font-size: 13px;
        }
        @include screen(tablet-2x) {
          font-size: 26px;
          max-width: 588px;
        }
      }
      & .card__icon {
        display: none;
        @include screen(tablet-2x) {
          display: block;
        }
      }
    }
    &-with-us {
      background-color: $green-bg;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      @include screen(tablet) {
        order: 2;
      }
      &:hover .card__title {
        color: transparent;
        background-clip: text;
        background-image: linear-gradient(167deg, #111415 0%, rgba(80, 68, 254, 0.48) 78.38%, #111415 100%);
      }
      & .card__title {
        font-weight: 900;
        font-family: 'RFDEWI';
        font-size: 23px;
        line-height: 90%;
        color: $black-bg;
        text-align: center;
        transition: color $transition-default, background-image $transition-default, background-clip $transition-default;
        @include screen(tablet) {
          font-size: 25px;
          line-height: 100%;
        }
        @include screen(tablet-2x) {
          font-size: 48px;
        }
      }
      & .card__icon {
        display: none;
        @include screen(tablet) {
          display: block;
        }
      }
    }
    &-social {
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 80px;
      grid-column: span 1 / span 1;
      @include screen(tablet) {
        min-height: 84px;
      }
      @include screen(tablet-2x) {
        min-height: 157px;
      }
      &:hover .card__img-social {
        opacity: 0.7;
      }
      & .card__img-social {
        transition: opacity $transition-default;
        @include screen(tablet-2x) {
          height: 40px;
          width: auto;
          object-fit: contain;
        }
      }
    }
  }
  &__social {
    grid-column: span 6 / span 6;
    display: grid;
    grid-template-columns: repeat(2, minmax(64px, 80px));
    gap: 8px;
    @include screen(tablet) {
      grid-column: span 4 / span 4;
      grid-template-columns: repeat(2, 84px);
      gap: 20px;
      order: 1;
    }
    @include screen(tablet-2x) {
      gap: 10px;
      grid-template-columns: repeat(2, 157px);
    }
    @include screen(laptop) {
      gap: 38px;
    }
  }
  &__slider {
    @include screen(tablet) {
      display: none;
    }
    @include screen(tablet-2x) {
      display: block;
    }
  }
  &-form {
    display: none;
    @include screen(tablet) {
      display: grid;
      visibility: hidden;
      opacity: 0;
      max-height: 0;
      z-index: -100;
      transition: $transition-default;
    }
    &.open {
      visibility: visible;
      opacity: 1;
      max-height: 188px;
      margin-bottom: 18px;
      z-index: 0;
      @include screen(tablet-2x) {
        margin-bottom: 32px;
        max-height: 352px;
      }
    }
    &__card {
      align-items: flex-start;
      @include screen(tablet-2x) {
        padding: 36px 24px;
      }
    }
    & .card__image {
      width: 100%;
      height: 100%;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }

    & .card__title {
      margin-bottom: 10px;
      font-family: "RFDEWI";
      font-weight: 900;
      font-size: 20px;
      @include screen(tablet-2x) {
        margin-bottom: 38px;
        font-size: 32px;
      }
    }
    & .form__input-comment {
      margin-bottom: 18px;
      @include screen(tablet-2x) {
        margin-bottom: 49px;
      }
    }
    & .form__btn {
      padding: 10px 30px;
      border-radius: 10px;
      @include screen(tablet-2x) {
        padding: 13px 70px;
      }
      @include screen(laptop) {
        padding: 13px 104px;
      }
    }
    & .form__fieldset-bottom {
      gap: 40px;
    }
    & .checkbox__text {
      font-size: 10px;
      @include screen(tablet-2x) {
        font-size: 18px;
      }
      &::after {
        top: 5px;
        @include screen(tablet-2x) {
          top: 8px;
        }
        @include screen(laptop) {
          top: 7px;
        }

      }
    }
  }
}

.green-text {
  font-weight: 700;
  color: $green-bg;
}

.gradient-text {
  font-weight: 700;
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(357deg, #bdf288 0%, #3d57f2 100%);
}