.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: $green-bg;
  background-image: url('/assets/images/popup/popup-bg.svg');
  background-position: 125px 322px;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: -100;
  visibility: hidden;
  opacity: 0;
  transition: opacity $transition-default, visibility $transition-default;
  @include screen(tablet) {
    display: none;
  }

  &.open {
    visibility: visible;
    opacity: 1;
    z-index: 100;
  }

  &__box {
    max-width: 390px;
    margin: 0 auto;
    padding: 24px 20px;

    &-top {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: 75px;
    }
  }

  &__btn {
    & svg {
      transition: opacity $transition-default;
    }
    &:active svg {
      opacity: 0.7;
    }

  }

  &__title {
    margin-bottom: 32px;
    font-family: 'RFDEWI';
    font-weight: 900;
    font-size: 32px;
    line-height: 100%;
    color: $black-bg;
  }



}