.form {
  &-tablet {
    display: none;
    @include screen(tablet) {
      display: block;
      width: 100%;
    }
  }

  &-title {
    color: $black-bg;
    font-family: 'RFDEWI';
    font-weight: 900;
    font-size: 23px;
    line-height: 100%;
    text-align: center;
    margin: auto 0;
    transition: color $transition-default, background-image $transition-default, background-clip $transition-default;
    @include screen(tablet) {
      margin: 0;
      margin-bottom: 7px;
      font-size: 11px;
      text-align: left;
    }
    @include screen(tablet-2x) {
      margin-bottom: 14px;
      font-size: 22px;
    }
    &::after {
      content: '!';
      @include screen(tablet) {
        content: '';
      }
    }
  }

  &-small-card {
    min-height: 169px;
    grid-column: span 6 / span 6;
    flex-direction: column;
    align-items: flex-start;
    background-color: $green-bg;

    @include screen(tablet) {
      min-height: 118px;
      grid-column: span 8 / span 8;
    }

    @include screen(tablet-2x) {
      min-height: 221px;
    }

    &:active .form-title {
      color: transparent;
      background-clip: text;
      background-image: linear-gradient(167deg, #111415 0%, rgba(80, 68, 254, 0.48) 78.38%, #111415 100%);
      @include screen(tablet) {
        color: $black-bg;
        background-image: none;
      }
    }
  }

  &__input {
    margin-bottom: 20px;
    padding: 4px 11px;
    width: 100%;
    border: 1px solid $black-bg;
    border-radius: $border-radius-mob;
    color: $black-bg;
    background-color: transparent;

    @include screen(tablet) {
      margin-bottom: 0;
      padding: 0;
      padding-bottom: 2px;
      border: none;
      border-radius: 0;
      border-bottom: 1px solid $black-bg;
    }
    &-name {
      @include screen(tablet) {
        width: 131px;
      }
      @include screen(tablet-2x) {
        width: 246px;
      }
    }
    &-comment {
      @include screen(tablet) {
        margin-bottom: 8px;
      }
      @include screen(tablet-2x) {
        margin-bottom: 17px;
      }
    }
    &::placeholder {
      font-family: "Roboto Mono";
      font-weight: 400;
      font-size: 16px;
      color: rgba(17, 20, 21, 0.3);
      @include screen(tablet) {
        font-size: 9px;
      }
      @include screen(tablet-2x) {
        font-size: 18px;
      }
    }
  }

  &__fieldset {
    @include screen(tablet) {
      display: flex;
      gap: 36px;
      margin-bottom: 15px;
    }
    @include screen(tablet-2x) {
      gap: 68px;
      margin-bottom: 28px;
    }
    &-bottom {
      display: flex;

      gap: 32px;
      flex-direction: column-reverse;
      @include screen(tablet) {
        flex-direction: row;
        align-items: center;
        gap: 58px;
      }
      @include screen(tablet-2x) {
        gap: 108px;
      }
    }
  }

  &__btn {
    padding: 13px;
    border-radius: $border-radius-mob;
    font-family: "Roboto Mono";
    font-size: 18px;
    background-color: $black-bg;
    @include screen(tablet) {
      padding: 6px 13px;
      border-radius: $border-radius-tab;
      font-size: 9px;
    }
    @include screen(tablet-2x) {
      padding: 11px 24px;
      border-radius: $border-radius;
      font-size: 18px;
    }
    &:hover,
    &:active {
      background-color: #40474a;
    }
  }
}

.checkbox {
  position: relative;
  cursor: pointer;

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  &__text {
    display: flex;
    align-items: flex-start;
    padding-left: 33px;
    position: relative;
    font-weight: 200;
    font-size: 14px;
    text-decoration: underline;
    color: $black-bg;
    @include screen(tablet) {
      align-items: center;
      padding-left: 16px;
      font-size: 6px;
    }
    @include screen(tablet-2x) {
      padding-left: 30px;
      font-size: 12px;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 22px;
      height: 22px;
      border: 1px solid $black-bg;
      border-radius: 100%;

      @include screen(tablet) {
        width: 10px;
        height: 10px;
      }

      @include screen(tablet-2x) {
        width: 18px;
        height: 18px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      left: 3px;
      top: 3px;
      width: 16px;
      height: 16px;
      border-radius: 100%;
      background-color: $black-bg;
      opacity: 0;
      transition: opacity $transition-default;

      @include screen(tablet) {
        left: 2px;
        top: 1px;
        width: 6px;
        height: 6px;
      }

      @include screen(tablet-2x) {
        left: 3px;
        top: 3px;
        width: 12px;
        height: 12px;
      }
    }
  }

  &__input:checked + .checkbox__text::after {
    opacity: 1;
  }
}