.hero {
  margin-bottom: 16px;
  @include screen(tablet) {
    margin-bottom: 18px;
  }
  @include screen(tablet-2x) {
    margin-bottom: 32px;
  }

  & .card {
    .card__image {
      width: 100%;
      height: 100%;

      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
    &-gif {
      display: none;
      @include screen(tablet) {
        display: block;
      }
    }
    &__hero-top {
      .card__title {
        margin-bottom: 0;
        font-family: 'RFDEWI';
        font-weight: 900;
        font-size: 22px;
        @include screen(tablet) {
          margin-bottom: 40px;
          font-weight: 800;
          font-size: 38px;
        }
        @include screen(tablet-2x) {
          margin-bottom: 76px;
          font-weight: 900;
          font-size: 72px;
        }
      }
      .card__tags {
        display: none;
        @include screen(tablet) {
          display: flex;
          align-items: flex-end;
          gap: 10px;
          bottom: 13px;
          left: 13px;
        }
        @include screen(tablet-2x) {
          display: flex;
          bottom: 24px;
          left: 24px;
          gap: 16px;
        }
      }
    }
    &__hero-bottom {
      .card__content {
        height: 100%;
        justify-content: flex-start;
      }
      .card__title {
        margin-bottom: 13px;
        font-family: 'RFDEWI';
        font-weight: 800;
        font-size: 18px;
        @include screen(tablet) {
          margin-bottom: 10px;
          font-size: 20px;
        }
        @include screen(tablet-2x) {
          margin-bottom: 14px;
          font-size: 38px;
        }
      }
      .card__description {
        font-size: 14px;
        line-height: 100%;
        color: $white;
        @include screen(tablet-2x) {
          font-size: 20px;
        }
        @include screen(laptop) {
          font-size: 28px;
        }
      }
    }
    &__title-color-green {
      & .card__title {
        color: $green-bg;
      }
    }
    &__title-color-white {
      & .card__title {
        color: $white;
      }
    }
    &__title-color-blue {
      & .card__title {
        color: #0041d7;
      }
    }
  }


}