.case-cards {
  grid-auto-flow: dense;

  & .card:nth-child(2n + 2) {
    grid-column: span 12 / span 12;
  }

  & .card:nth-child(2n + 3) {
    grid-column: span 6 / span 6;
  }

  @include screen(tablet) {
    gap: 18px;

    .card.col-span-4 {
      grid-column: span 4 / span 4;
    }

    .card.col-span-6 {
      grid-column: span 6 / span 6;
    }

    .card.col-span-8 {
      grid-column: span 8 / span 8;
    }

    .card.col-span-12 {
      grid-column: span 12 / span 12;
    }
  }

  & .card-with-text {
    .card__content {
      padding-top: 10px;
      height: 100%;
      justify-content: space-between;
    }
    .card__description {
      max-width: 297px;
      font-weight: 400;
      font-size: 12px;
      text-transform: uppercase;
      & span {
        font-weight: 900;
      }
      @include screen(tablet-2x) {
        max-width: 593px;
        font-size: 24px;
      }
    }
  }
  & .card-blue-bg {
    background-color: $blue-bg;
  }
}